import React from "react";
import "../App.css";
import DropMenu from "./Menu";

function BigMenu(props) {
    let bigme = [];
    for (var i = 1; i <= props.num; i++) {
        bigme.push(<DropMenu name={props.name + "-" + i} num={i}></DropMenu>);
    }
    return (
            <li>
                <a href={props.href}>{props.name}</a>
                <ul>{bigme}</ul>
            </li>


    );
}

export default BigMenu;
